import Note from './components/Note';
import Title from './components/Title';
import { useState, useEffect } from 'react';
import axios from 'axios';
import noteService from './services/notes';
import io from 'socket.io-client';
import {
  Container,
  Row,
  Col,
  Button,
  Alert,
  Stack,
  Card,
  ListGroup,
} from 'react-bootstrap';

const App = () => {
  const [notes, setNotes] = useState([]);
  const [newNote, setNewNote] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [socket, setSocket] = useState(null);
  const [newMessage, setNewMessage] = useState(null);

  const setupSocket = () => {
    const newSocket = io('/');
    newSocket.on('connect', () => {
      //makeToast("success", "Socket Connected!");
      console.log('Socket connected');
    });

    newSocket.on('newMessage', () => {
      setNewMessage(true);
    });

    newSocket.on('disconnect', () => {
      setSocket(null);
      setTimeout(setupSocket, 3000);
      //makeToast("error", "Socket Disconnected!");
    });

    setSocket(newSocket);
  };

  useEffect(() => {
    setupSocket();
    noteService.getAll().then((initialNotes) => {
      console.log('GET request succesful');
      setNotes(initialNotes);
    });
  }, []); // Empty array means that it runs only on the first render

  useEffect(() => {
    noteService.getAll().then((initialNotes) => {
      setNotes(initialNotes);
      setNewMessage(false);
    });
  }, [newMessage]); // Empty array means that it runs only on the first render

  const notesToShow = showAll ? notes : notes.filter((note) => note.important);

  const addNote = (event) => {
    event.preventDefault();
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
      id: notes.length + 1,
    };

    noteService.create(noteObject).then((returnedNotes) => {
      console.log('POST request succesful');
      setNewNote('');
    });
  };

  const handleNoteChange = (event) => {
    setNewNote(() => event.target.value);
  };

  const toggleImportant = () => {
    setShowAll((showAll) => !showAll);
  };

  const toggleImportanceOf = (id) => {
    const url = `/api/notes/${id}`;
    const note = notes.find((n) => n.id === id);
    const changedNote = { ...note, important: !note.important };

    noteService
      .update(id, changedNote)
      .then((returnedNotes) => {
        console.log('Note object updated using PUT');
        setNotes(notes.map((note) => (note.id !== id ? note : returnedNotes)));
      })
      .catch((error) => {
        alert(`the note '${note.content}' was already deleted from server`);
        setNotes(notes.filter((n) => n.id !== id));
      });
  };

  return (
    <div>
      <Container fluid>
        <Row>
          <Title text="SOLAITRA" />
        </Row>
        <Row className="justify-content-md-center">
          <Card
            className="card text-white bg-dark mb-3 mx-auto"
            style={{ width: '65.65%' }}
          >
            <ListGroup variant="flush">
              {notesToShow.map((note) => (
                <Note
                  key={note.id}
                  note={note}
                  className="d-flex justify-content-center"
                  toggleImportance={() => toggleImportanceOf(note.id)}
                />
              ))}
            </ListGroup>
          </Card>
        </Row>

        <Stack gap={2} className="col-md-8 mx-auto">
          <form onSubmit={addNote}>
            <Stack gap={2} className="col-auto mx-auto">
              <input
                className="form-control"
                value={newNote}
                onChange={handleNoteChange}
              />{' '}
              {/* controlled component: the state of the input is controlled by React by adding a react state as the value of the input and an event handler containing setState() to update the content */}
              <Button variant="secondary" type="submit">
                save
              </Button>
            </Stack>
          </form>
          <Button variant="secondary" onClick={toggleImportant}>
            show {showAll ? 'important' : 'all'}
          </Button>
        </Stack>
      </Container>
    </div>
  );
};

export default App;
